.ketqua__0 span {
  position: relative; }
  .ketqua__0 span:before {
    content: "";
    width: 66px;
    height: 1px;
    background-color: #E03737;
    position: absolute;
    left: calc(100% + 14px);
    top: 0;
    bottom: 0;
    margin: auto; }
  .ketqua__0 span:after {
    content: "";
    width: 66px;
    height: 1px;
